html, body, .app, #root, .app-layout {
  width: 100%;
  height: 100%;
  direction: rtl;
  overflow: hidden;
  box-sizing: border-box;
  font-family: Heebo;
}

.basad {
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  margin: 1em;
}

.app-bg {
  position: absolute;
  background-color: #DE9433;
  width: 100%;
  height: 100%;
}

.finally {
  font-weight: bold;
}

.bg-image {
  background-image: url('./img/bg_image.png');
}

.bg-image, .bg-image-fade {
  position: absolute;
  height: 60%;
  width: 100%;
  background-size: cover;
  background-position: bottom;
}

.bg-image-fade {
  position: absolute;
  height: 60%;
  width: 100%;
  background-size: cover;
}

.app-layout {
  position: absolute;
  display: grid;
}

.logo-image, .logo-name {
  background-repeat: no-repeat;
  background-size: contain;
}

.logo-image {
  background-image: url('img/logo_image.png');
}
.logo-name {
  background-image: url('img/logo_text.png');
}

$breakpoint: 900px;

@media only screen and (min-width: $breakpoint) {
  .bg-image-fade {
    background: linear-gradient(transparent 50%, black);
  }

  .app-layout {
    grid-template: ".    .    .  " 138fr
                   ".    .    app" 200fr
                   "logo tag  app" 280fr
                   ".    desc app" 195fr
                   ".    .    .  " 268fr / 6fr 4fr 6fr;
  }

  .logo-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .logo-image, .logo-name {
    width: 10em;
  }

  .tagline {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
    padding-bottom: .4em;
  }

  .tagline, .description {
    padding-right: 3em;
    font-size: 20px;
  }

  .phone {
    align-items: flex-start;
  }

  .description {
    align-items: flex-start;
  }


  .logo-image {
    flex: 4;
  }

  .logo-name {
    flex: 1;
  }

  .store-buttons img {
    height: 3em;
  }
}

@media only screen and (max-width: $breakpoint) {
  .bg-image-fade {
    background: linear-gradient(black, transparent 50%);
  }

  .app-layout {
    grid-template: "logo" 160fr
                 "tag" 90fr
                 "app" 670fr
                 "desc" 160fr / 1fr;
  }

  .store-buttons img {
    height: 2.5em;
  }

  .tagline {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  .tagline, .description {
    font-size: 20px;
  }

  .logo-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .logo-image {
    background-position: center;
    height: 5em;
    width: 4em;
  }

  .logo-name {
    background-position: right;
    height: 2.25em;
    width: 6em;
    margin-top: 2em;
    margin-right: 1em;
  }

  .phone {
    align-items: center;
  }

  .description {
    align-items: center;
  }
}

.logo-container {
  grid-area: logo;
  overflow: hidden;
}

.logo-image {
  max-height: 100%;
  object-fit: contain;
}

.tagline {
  grid-area: tag;
  color: white;
  text-align: center;
}

.description {
  grid-area: desc;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}

.description-text {
  margin-bottom: .5em;
}

.store-buttons {
  display: flex;
  flex-direction: row;;
}

.store-buttons img {
  cursor: pointer;
}

.store-buttons img:not(:first-child) {
  margin-left: .5em;
}

.phone {
  grid-area: app;
  overflow: hidden;
  -webkit-box-reflect: below 1em linear-gradient(to bottom, rgba(0,0,0,0) 70%, rgba(0,0,0,.2));
}
.phone {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.phone img {
  max-height: 100%;
  object-fit: contain;
}